import React, { Component } from "react";
import Dialog from "@material-ui/core/Dialog";
import AppBar from "@material-ui/core/AppBar/AppBar";
import MuiThemeProvider from "@material-ui/core/styles/MuiThemeProvider";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
//import hero from "@wfp/ui/assets/internal";

class FormUserDetails extends Component {
  continue = e => {
    e.preventDefault();
    this.props.nextStep();
  };
  render() {
    const { values, handleChange } = this.props;
    return (
      <MuiThemeProvider>
        <React.Fragment>
          <AppBar title="Employee User Details" fullWidth="true">
            {/*  //<img src="./add.svg" /> */}
            <h1> Employee User Details</h1>
          </AppBar>
          <br />
          <br />
          <br />
          <br />
          <TextField
            placeholder="Enter your firstName"
            label="FIRSTNAME"
            onChange={handleChange("firstName")}
            defaultValue={values.firstName}
            margin="normal"
          />
          <br />
          <TextField
            placeholder="Enter your lastName"
            label="LASTNAME"
            onChange={handleChange("lastName")}
            defaultValue={values.lastName}
            margin="normal"
            //fullWidth="true"
          />
          <br />
          <TextField
            placeholder="Enter your Email"
            label="Email"
            onChange={handleChange("email")}
            defaultValue={values.Email}
            margin="normal"
            // fullWidth="true"
          />
          <br />
          <TextField
            placeholder="Enter your division"
            label="Division"
            onChange={handleChange("division")}
            defaultValue={values.division}
            margin="normal"
            // fullWidth="false"
          />
          <br />
          <TextField
            placeholder="Enter your Country Office"
            label="COUNTRY OFFICE"
            onChange={handleChange("countryOffice")}
            defaultValue={values.countryOffice}
            margin="normal"
            // fullWidth="false"
          />
          <br />
          <TextField
            placeholder="Enter your BIO DATA"
            label="BIO DATA"
            onChange={handleChange("bio")}
            defaultValue={values.bio}
            margin="normal"
            // fullWidth="false"
          />
          <br />
          <br />
          <Button color="primary" variant="contained" onClick={this.continue}>
            CONTINUE
          </Button>
        </React.Fragment>
      </MuiThemeProvider>
    );
  }
}
export default FormUserDetails;
